import ajax from '@/common/lib/ajax'
import config from '@/config'

export function webLogin(key, params = {}) {
  return ajax.get(config.urlConfig.webLogin + key, {
    params
  });
}

export function getTemplates(params = {}) {
  return ajax.post(config.urlConfig.getTemplates, {
    params
  });
}

export function getTemplate(id, params = {}) {
  return ajax.get(config.urlConfig.getTemplate+id, {
    params
  });
}

export function createNewTemplate(params = {}) {
  return ajax.put(config.urlConfig.createNewTemplate, {
    params
  });
}

export function updateTemplate(params = {}) {
  return ajax.put(config.urlConfig.createNewTemplate, {
    params
  });
}

export function deleteTemplate(id, params = {}) {
  return ajax.del(config.urlConfig.deleteTemplate+id, {
    params
  });
}

export default {
  webLogin,
  getTemplates,
  createNewTemplate,
  updateTemplate,
  deleteTemplate
}



