<template>
  <div class="login-container">
    <div class="login-wrapper">
      <div>web登录码：<input v-model="webCode" type="text"/></div>
      <el-button style="width: 90px; padding: 10px;" type="primary" @click="doLogin">登 录</el-button></div>
    </div>
  </div>
</template>

<script>
import {webLogin} from '@/api';


export default {
  name: 'HelloWorld',
  components: {
  },
  data () {
    return {
      webCode: ''
    }
  },
  methods: {
    doLogin() {
      webLogin(this.webCode, {}).then((res) => {
        if(res.code == 'success') {
          window.location.href = './'
        } else if(res.code) {
          this.$message(res.msg || res.message);
        } else {
          this.$message('服务器开小差了');
        }
      });
    }
  },
  mounted() {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.login-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-wrapper {
  width: 400px;
  height: 300px;
  background-color: #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.login-wrapper button {
  margin-top: 20px;
}
</style>



