<template>
  <div class="visitor-data-table">
    <table border="0" v-if="visitors.length > 0 && datas.length > 0">
      <tr>
        <th style="background-color: #f5f5f5; color:#000; padding: 10px; border: solid 1px #f5f5f5;">数据项</th>
        <th style="background-color: #f5f5f5; color:#000; padding: 10px; border: solid 1px #f5f5f5;" v-for="(visitor, index) in visitors">{{visitor.name}}</th>
      </tr>
      <tr v-for="(data, index) in datas">
        <td style="padding: 0px; padding: 10px; border-right: solid 1px #f5f5f5;">{{data.name}}</td>
        <td style="padding: 0px; padding: 10px; border-right: solid 1px #f5f5f5;" v-for="(visitor, index) in visitors">{{visitorDataMapStr.indexOf(data.name+decorate+visitor.name)>-1 ? '*' : ''}}</td>
      </tr>
    </table>
  </div>
</template>
<script >

export default {
	name: 'visitor-data-table',
	componentName: 'visitor-data-table',
	components: {
  },
  props: ['visitors', 'datas', 'visitorDataMap'],
    methods:{
  },
	created(){

	},
	data() {
    return {
      decorate: '$%&%$'
    }
	},
	computed: {
	  visitorDataMapStr() {
	    var mStr = [], m = [].concat(this.visitorDataMap);
	    m.forEach((item) => {
	      mStr.push(item.dataName + this.decorate + item.visitName);
	    });
	    return mStr;
	  }
	},
  mounted(){
  }
}

</script>

<style>
.visitor-data-table {
  width: 100%;
  height: 100%;
  font-size: 14px;
}
</style>



