<template>
  <div class="edit-view">
    <div class="edit-view-guide">
      <div class="edit-view-step current">1</div>
      <div class="current">添加数据项</div>
      <div :class="'edit-view-line current'"></div>
      <div :class="'edit-view-step' + ((current > 1) ? ' current' : '')">2</div>
      <div :class="(current > 1) ? ' current' : ''">选择单次访问内容</div>
      <div :class="'edit-view-line' + ((current > 1) ? ' current' : '')"></div>
      <div :class="'edit-view-step' + ((current > 2) ? ' current' : '')">3</div>
      <div :class="(current > 2) ? ' current' : ''">生成SDV表</div>
    </div>
    <div class="step-wrapper" v-if="current == 1">
      <div class="edit-view-input"><input class="step-title" type="text" v-model="templateName" placeholder="请输入SDV模板标题"/><div class="step-help" @click="dataHelp(true)">?</div></div></p>
      <input type="text" v-model="templateRemark" placeholder="请输入SDV模板简介"/>
      <div class="edit-view-input-separator"></div>
      <textarea v-model="datas" style="flex: 1; outline: none; border: none;" placeholder="请输入数据项（每个数据项换行分隔）">
      </textarea>
      <div style="display: flex;">
        <el-button style="width: 90px; padding: 10px; margin-right:20px;" type="primary" @click="step1Next">下一步</el-button>
      </div>
    </div>
    <div class="step-wrapper" style="padding: 0px;" v-if="current == 2">
      <div class="step-2-btns">
        <div @click="showVisitor(true)">添加访视</div>
        <div v-if="selectedVisitors.length > 0" @click="deleteVisitor">删除访视</div>
      </div>
      <el-table
        class="step-2-table"
        ref="multipleTable"
        :data="visitors"
        tooltip-effect="dark"
        style="width: 100%; margin-bottom: 20px;"
        @selection-change="tableSelectionChange">
        <el-table-column
          type="selection"
          align="left"
          width="55">
        </el-table-column>
        <el-table-column
          label="访视序列"
          align="left"
          width="120">
          <template slot-scope="scope">{{ scope.row.vIndex }}</template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="访视名称"
          align="left"
          width="120">
        </el-table-column>
        <el-table-column
          prop="vDataStr"
          label="访视内容"
          align="left"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <div style="display: flex;">
        <el-button style="width: 90px; padding: 10px; margin-right:20px;" type="primary" @click="dialogVisible = true;">上一步</el-button>
        <el-button style="width: 90px; padding: 10px; margin-right:20px;" type="primary" @click="step2Next">下一步</el-button>
      </div>
    </div>
    <div class="step-wrapper" v-if="current == 3">
      <div class="template-title">{{templateName}}</div>
      <div class="template-desc">{{templateRemark}}</div>
      <visitor-data-table style="margin-top: 30px; flex:1; padding-bottom: 30px; border-bottom: solid 1px #eee;" :visitors="visitors" :datas="dataObjArr" :visitorDataMap="visitDatas"></visitor-data-table>
      <div style="display: flex;">
        <el-button style="width: 90px; padding: 10px; margin-right:20px;" type="primary" @click="current = 2">上一步</el-button>
        <el-button style="width: 90px; padding: 10px; margin-right:20px;" type="primary" @click="goFinish">完成</el-button>
      </div>
    </div>
    <div class="step-1-panel" v-if="dataPanelShow">
      <div class="step-panel-close" @click="dataHelp(false)"></div>
      <div class="step-1-panel-title">SDV模板事例</div>
      <div><font color="red" style="margin-right:10px;">*</font>SDV模板标题</div>
      <div class="step-1-panel-line"></div>
      <div style="margin-left: 20px;">研究前筛选</div>
      <div><font color="red" style="margin-right:10px;">*</font>SDV模板简介</div>
      <div class="step-1-panel-line"></div>
      <div style="margin-left: 20px;text-align: left;">近来，新的药物治疗靶点，CTLA-4，PD-1及其配体PD-L1在许多肿瘤中得到了积极的研究。</div>
      <div style="margin-top: 20px;"><font color="red" style="margin-right:10px;">*</font>请输入数据项</div>
      <div class="step-1-panel-line"></div>
      <div style="margin-left: 20px;">EGC</div>
      <div style="margin-left: 20px;">病史</div>
      <div style="margin-left: 20px;">体格检查</div>
      <div style="margin-left: 20px;">体力状况</div>
      <div style="margin-left: 20px;">血液学</div>
      <div style="margin-left: 20px;">血生化</div>
      <div style="margin-left: 20px;">局部症状</div>
      <div style="margin-left: 20px;">生活质量</div>
      <div style="margin-left: 20px;">其他检查</div>
    </div>
    <div class="step-2-panel" v-if="visitorPanelShow">
      <div class="step-panel-close" @click="showVisitor(false)"></div>
      <div class="step-2-panel-title">添加访视</div>
      <div><font color="red" style="margin-right: 5px;">*</font>输入访视名称</div>
      <input v-model="visitName" placeholder="例：治疗转移性肾细胞癌二期临床试验"/>
      <div><font color="red" style="margin-right: 5px;">*</font>选择访视数据项（可多选）</div>
      <el-checkbox-group class="step-2-checkbox-group" v-model="checkedDatas" @change="handleCheckDatasChange">
        <el-checkbox v-for="(item, index) in dataArr" :label="index" :key="index">{{item}}</el-checkbox>
      </el-checkbox-group>
      <div class="step-2-panel-btn">
        <div class="web-yl-btn" @click="addVisitor">添加</div>
      </div>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%">
      <span>回到上一步会清除当前访视记录</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="() => {this.dialogVisible = false; this.toStep1();}">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script >
import VisitorDataTable from '../components/visitor-data-table.vue';
import {createNewTemplate} from '../api';
var emptyData = {
  dialogVisible: false,
  templateName: '',
  templateRemark: '',
  visible: false,
  current: 1,
  visitorPanelShow: false,
  dataPanelShow: false,
  datas: '',
  dataArr: [],
  dataObjArr: [],
  checkedDatas: [],
  visitName: '',
  visitors: [],//{vIndex: '', name: '', vDataIndexes: [], vDataStr: ''}
  visitDatas: [],
  selectedVisitors: []
}

export default {
	name: 'edit-view',
	componentName: 'edit-view',
	components: {
	  VisitorDataTable
  },
	created(){

	},
	data() {
      return {
          dialogVisible: false,
          templateName: '',
          templateRemark: '',
          visible: false,
          current: 1,
          visitorPanelShow: false,
          dataPanelShow: false,
          datas: '',
          dataArr: [],
          dataObjArr: [],
          checkedDatas: [],
          visitName: '',
          visitors: [],//{vIndex: '', name: '', vDataIndexes: [], vDataStr: ''}
          visitDatas: [],
          selectedVisitors: []
    	}
	},
  mounted(){
  },
  props: {

  },
  methods:{
    open(){
      this.visible = true;
      this.$emit('open');
    },
    showVisitor(v) {
      this.visitorPanelShow = v;
      this.$emit('showmask', v);
    },
    dataHelp(v) {
      this.dataPanelShow = v;
      this.$emit('showmask', v);
    },
    step1Next() {
      var dataObjArr = [], hasSame = false,
          datas = this.datas.replace(/\n{2,}/g, '\n'),
          dataArr = datas.split('\n');
      if(dataArr[0] == '') {
          dataArr.shift();
      }
      if(dataArr[dataArr.length-1] == '') {
          dataArr.pop();
      }
      if(!this.templateName || !this.templateRemark) {
          this.$message('请输入模板名称或模板描述');
          return;
      }
      if(dataArr.length < 1) {
          this.$message('请按照格式输入数据项');
          return;
      }
      dataArr.forEach((d) => {
          dataObjArr.push({name: d});
          var regx = new RegExp(d, 'g');
          var result = datas.match(regx);
          if((!result ? 0 : result.length) > 1) {
             hasSame = true;
          }
      });
      if(hasSame) {
          this.$message('数据项名称不能重复');
          return;
      }
      this.dataArr = dataArr;
      this.dataObjArr = dataObjArr;
      this.datas = datas;
      this.current = 2;
      console.log(dataObjArr);
    },
    toStep1() {
      this.current = 1;
      this.visitors = [];
    },
    step2Next() {
      var visitors = [].concat(this.visitors), res = [];
      visitors.forEach((v) => {
        var dataIndexes = [].concat(v.vDataIndexes);
        dataIndexes.forEach((d) => {
           res.push({
             dataName: this.dataObjArr[d].name,
             visitName: v.name
           });
        });
      });
      this.visitDatas = res;
      this.current = 3;
    },
    goFinish() {
      //:visitors="visitors" :datas="dataObjArr" :visitorDataMap="visitDatas"
      console.log('goFinish');
      var visitDatas = this.visitDatas,
          datas = this.dataObjArr,
          visits = this.visitors,
          name = this.templateName,
          remark = this.templateRemark;
      //TODO
      createNewTemplate({name, remark, datas, visits, visitDatas}).then((res) => {
         if(res.code == 'success') {
           this.$message('保存成功！');
           this.$emit('save');
         } else if(res.code) {
           this.$message(res.msg || res.message);
         } else {
           this.$message('服务器开小差了');
         }
      });
    },
    clearAll() {
        Object.assign(this, emptyData);
    },
    handleCheckDatasChange(values) {
      console.log(values);
    },
    addVisitor() {
      console.log(this.checkedDatas);
      if(!this.visitName) {
        this.$message('请输入访视名称');
        return ;
      }
      if(this.checkedDatas.length < 1) {
        this.$message('至少选择一个数据项');
        return ;
      }
      var hasSameName = false;
      this.visitors.forEach((v) => {
         if(!hasSameName) {
           hasSameName = (v.name == this.visitName);
         }
      });
      if(hasSameName) {
         this.$message('访视名称已存在');
         return ;
      }
      var index = this.visitors.length,
        vIndex = '访视'+(this.visitors.length + 1),
        vDataIndexes = this.checkedDatas,
        name = this.visitName,
        vDataStr = '',
        visitor = {index, vIndex, name, vDataIndexes};
      vDataIndexes.forEach((k) => {
        vDataStr += (this.dataArr[k]+'、');
      });
      visitor.vDataStr = vDataStr;
      this.visitors.push(visitor);
      this.showVisitor(false);
    },
    deleteVisitor() {
      this.selectedVisitors.forEach((item) => {
        this.visitors.splice(item.index, 1);
      });
      this.selectedVisitors = [];
    },
    tableSelectionChange(v) {
      console.log('tableSelectionChange');
      console.log(v);
      this.selectedVisitors = v;
    }
  }
}

</script>

<style>
.edit-view {
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #999;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 15px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.edit-view input {
  outline:none;
  border: none;
}
.edit-view-guide {
  display: flex;
  padding-top: 15px;
  padding-bottom: 15px;
  box-sizing: border-box;
  border-bottom: solid 1px #eee;
  margin-bottom: 10px;
}
.edit-view-guide .current {
  color: #00acff;
}
.edit-view-input {
  display: flex;
}
.edit-view-input input {
  flex: 1;
}
.edit-view-guide input {
  padding-top: 15px;
  padding-bottom: 15px;
  width: 100%;
}
.step-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
}
.step-title {
  font-size: 24px;
  color: #00acff;
}
.step-wrapper .template-title {
  display: flex;
  font-size: 16px;
  align-items: left;
  color: #00acff;
}
.step-wrapper .template-desc {
  display: flex;
  font-size: 12px;
  align-items: left;
  margin-top: 20px;
}
.step-help {
  width: 20px;
  height: 20px;
  background-color: red;
  color: #fff;
  text-align: center;
  border-radius: 10px;
}
.step-1-panel {
  position: fixed;
  z-index: 1000;
  right: 0px;
  height: 100%;
  width: 300px;
  display: block;
  flex-direction: column;
  background-color: #fff;
  padding-left: 20px;
  padding-right: 20px;
  overflow-y: auto;
}
.step-1-panel > div {
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-start;
}
.step-1-panel > div:nth-child(2) {
  justify-content: center;
}
.step-1-panel-title {
  padding: 20px;
  font-size: 20px;
  color: #111;
  margin-bottom: 20px;
}
.step-1-panel > div:first-child {
  justify-content: center;
}
.step-1-panel-line {
  border: solid 1px #eee;
}
.step-2-panel {
  position: fixed;
  z-index: 1000;
  right: 0px;
  height: 100%;
  width: 300px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding-left: 20px;
  padding-right: 20px;
}
.step-2-panel > div {
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-start;
}
.step-2-panel > div:first-child {
  justify-content: center;
}
.step-2-panel-title {
  padding: 20px;
  font-size: 20px;
  color: #111;
  margin-bottom: 20px;
}
.step-2-panel .step-2-panel-btn {
  display: flex;
  justify-content: center;
}
.step-2-checkbox-group {
  display: flex;
  flex-wrap: wrap;
  margin-left: 20px;
}
.step-2-checkbox-group label {
  width: 130px;
  margin-left: 0px !important;
  display: flex;
  justify-content: flex-start;
}
.step-panel-close {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
  background-image: url('../assets/x@2x.png');
  background-size: 20px 20px;
}
.step-2-btns {
  margin-top: 30px;
  display: flex;
}
.step-2-btns div {
  margin-right: 15px;
  width: 85px;
  height: 30px;
  border-radius: 3px;
  background-color: red;
  font-size: 12px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.step-2-btns div:first-child {
  background-color: #ffca59;
}
.step-2-table {
  margin-top: 30px;
}
.step-2-panel input {
  margin-bottom: 30px;
}
.edit-view-input-separator {
  margin-top: 10px;
  margin-bottom: 10px;
  border-bottom: solid 1px #eee;
}
.edit-view-step {
  width: 22px;
  height: 22px;
  border-radius: 11px;
  color: #999;
  background-color: #dddddd;
  display: flex;
  align-items: center;
  justify-content: center;
}
.edit-view-step.current {
  background-color: #00acff;
  color: #fff;
}
.edit-view-line {
  width: 80px;
  height: 3px;
  margin: 10px;
  background-color: #999;
  box-sizing: border-box;
}
.edit-view-line.current{
  background-color: #00acff;
}
</style>



