  name: 'HelloWorld',
  components: {
    EditView,
    VisitorDataTable
  },
  data () {
    return {
      qcodeShow: false,
      searchData: '',
      maskShow: false,
      type: '',
      searchList: [],
      list: [],
      trashList: [],
      currentListIndex: -1,
      currentTemplateDetail: {}
    }
  },
  methods: {
    showMask: function(v) {
      this.maskShow = v;
      console.log(this.maskShow);
    },
    folderSwitch(type) {
      this.type = type;
      this.currentListIndex = -1;
      this.currentTemplateDetail = {};
    },
    deleteTemplate(isTrue) {
      if(isTrue) {
        this.deleteTemplateComplete();
        return;
      }
      updateTemplate({id: this.currentTemplateDetail.id, isDelete: 1}).then((res) => {
        console.log('updateTemplate');
        console.log(res);
        if(res.code == 'success') {
          this.currentTemplateDetail = {};
          this.currentListIndex = -1;
          this.getList();
          if(this.type == 'search') {
            this.doSearch();
          }
          this.getTrash();
        } else if(res.code) {
          this.$message(res.msg || res.message);
        } else {
          this.$message('服务器开小差了');
        }
      })
    },
    deleteTemplateComplete() {
      if(!this.currentTemplateDetail.id) {
        return ;
      }
      //TODO
      deleteTemplate(this.currentTemplateDetail.id).then((res) => {
        console.log('deleteTemplate');
        console.log(res);
        if(res.code == 'success') {
          this.currentTemplateDetail = {};
          this.currentListIndex = -1;
          this.getList();
          if(this.type == 'search') {
            this.doSearch();
          }
          this.getTrash();
        } else if(res.code) {
          this.$message(res.msg || res.message);
        } else {
          this.$message('服务器开小差了');
        }
      })
    },
    onTemplateSelected(index) {
      var list = {
        list: this.list,
        search: this.searchList,
        trash: this.trashList
      };
      this.currentListIndex = index;
      console.log(list[this.type][index].id);
      //this.currentTemplateDetail = list[this.type][index];
      //TODO
      getTemplate(list[this.type][index].id).then((res) => {
        if(res.code == 'success') {
          this.currentTemplateDetail = res.data;
        } else if(res.code) {
          this.$message(res.msg || res.message);
        } else {
          this.$message('服务器开小差了');
        }
      });
    },
    afterSave: function() {
      this.getList();
      this.folderSwitch('list');
    },
    doSearch: function() {
      //TODO
      getTemplates({"extendMap":{searchData: this.searchData}}).then((res) => {
        if(res.code == 'success') {
          this.searchList = res.data.result;
        } else if(res.code) {
          this.$message(res.msg || res.message);
        } else {
          this.$message('服务器开小差了');
        }
      });
    },
    getList: function() {
      //TODO
      getTemplates({isDelete: 0}).then((res) => {
       console.log(res);
       if(res.code == 'success') {
         this.list = res.data.result;
       } else if(res.code) {
         this.$message(res.msg || res.message);
       } else {
         this.$message('服务器开小差了');
       }
      });
    },
    getTrash: function() {
      //TODO
      getTemplates({isDelete: 1}).then((res) => {
        if(res.code == 'success') {
          this.trashList = res.data.result;
        } else if(res.code) {
          this.$message(res.msg || res.message);
        } else {
          this.$message('服务器开小差了');
        }
      });
    }
  },
  mounted() {
    this.getList();
    this.getTrash();
  }
}
</script>

